@use "../../assets/sass/_variables.scss" as *;

.counter-box{
    margin: 0.5rem;

    border: 1px solid $colour-black;
    border-radius: 0.25rem;

    .title{
        font-weight: bold;
    }

    .increments{
        & *{
            display: inline-block;
            margin: 0 1rem;
        }

        &__button{
            width: 1.5rem;
            height: 1.5rem;
            border-radius: 50%;

            background-color: $colour-secondary;
            border: 0.05rem solid $colour-black;

            font-weight: bold;
            text-align: center;
            line-height: 75%;

            user-select: none;

            &:active{
                background-color: $colour-clicked;
            }
        }
    }

    &--warning{
        background-color: $colour-warning;

        .increments__button{
            background-color: $colour-warning-button;
            &:active{
                background-color: $colour-warning-clicked;
            }
        }
    }

    &--emergency{
        background-color: $colour-emergency;
        color: $colour-text-secondary;

        .increments__button{
            background-color: $colour-emergency-button;
            color: $colour-text-secondary;

            &:active{
                background-color: $colour-black;
            }
        }
    }
}